.dashboard {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;

    background-color: #222;
}

.dashboard-piechart {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
}

.dashboard-functions {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.dashboard-functions-function {
    font-size: 0.8em;
    color: dimgrey;
    margin: 10px;
}

.dashboard-functions-function:hover {
    color: lightgrey;
}

.welcome-logo {
    width: 120px;
    margin: 0 0 20px 0;
}

.welcome-form {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.welcome input {
    flex-grow: 1;
    font-size: 0.8em;
    margin: 2px 0;
    padding: 2px 6px;
    border: 1px solid grey;
    border-radius: 4px;
}

.welcome-form-submit {
    background-color: darkgrey;
    color: black;
}

.welcome-form-submit:hover {
    background-color: lightgrey;
    color: black;
}

.welcome-change-mode {
    color: dimgrey;
    font-size: 60%;
    margin: 10px;
}

.welcome-change-mode:hover {
    color: lightgrey;
}